import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router";
import { getUserAPI, updateUserAPI } from "../../service/user.service";
import { useSelector } from "react-redux";

function UpdateUser() {
  let { id } = useParams()
  let [user, setUser] = useState({
    id, name: "",
    phone: "",
    password: "",
    address: "",
  });
  let navigate = useNavigate()
  let { users } = useSelector((store) => store.user)


  let getData = async () => {
    // let body = await getUserAPI(id)
    const body = users.find(item => item.id === parseInt(id))

    setUser(body)
  }

  useEffect(() => { getData() }, [id])

  let handleChangeText = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };
  let handleSubmit = async () => {
    await updateUserAPI(user)
    navigate("/users")
  };

  return (
    <div>
      <div>
        <label>ID: </label>
        <input value={id} readOnly />
        <br></br>
        <label>Name: </label>
        <input name="name" value={user.name} onChange={handleChangeText} />
        <br></br>
        <label>Phone: </label>
        <input name="phone" value={user.phone} onChange={handleChangeText} />
        <br></br>
        <label>Address: </label>
        <input name="address" value={user.address} onChange={handleChangeText} />
        <br></br>
        <button onClick={handleSubmit}>Save</button>
      </div>
    </div>
  );
}
export default UpdateUser;
