import { useEffect, useState } from "react";

function TestPage() {
  let [personList, setPersonList] = useState([{ id: 1, name: "A1" }]);
  let [editPerson, setEditPerson] = useState(null)

  return (
    <>
      <h1>Hello JSX</h1>
      {/* <Hi name="ReactJS 1" id="1"/> */}
      <From setPersonArray={setPersonList} editPerson={editPerson}/>
      <br />
      <Table setEditPerson = {setEditPerson} personArray={personList} setPersonArray={setPersonList} />
    </>
  );
}

function From({ setPersonArray, editPerson }) {
  // state - array
  let [id, setId] = useState("");
  let [name, setName] = useState("");

  useEffect(() => {
    setId(editPerson?.id || "")
    setName(editPerson?.name ||"")
  }, [editPerson])

  let onChangeHandler = (e) => {
    // console.log(e.target.value);
    // set gia tri cho bien id
    if (e.target.name === "id") setId(e.target.value);
    if (e.target.name === "name") setName(e.target.value);
  };

  let submitCreateForm = () => {
    console.log("Save");
    let person = { id, name };
    // console.log(person["id"]);
    //console.log(person.id);
    // Set State cua app component
    // add them vao mang
   
    //currentVar: gia tri hien tai cua state personList
    setPersonArray((currentVar) => [...currentVar, person]);
  };

  let submitUpdateForm = () => {
    console.log("Save");
    let person = { id, name };
    // console.log(person["id"]);
    //console.log(person.id);
    // Set State cua app component
    // add them vao mang
    // let newArr = [...personList, person]
    // setPersonArray(newArr);
    if(editPerson){
      setPersonArray(currentVar => {
        // 1. tim vi tri cua editperson trong mang
        const index = currentVar.findIndex(p => p.id === editPerson.id)
        
        // 2. thay the doi tuong person
        console.log(index);
        // thay the
        let newArr = [...currentVar]
        newArr[index] = person

        return newArr
      })

      return;
    }

  };

  // const inputError = { backgroundColor: "red" };
  // const inputValid = { backgroundColor: "green" };

  return (
    <div>
      <label> ID: </label>
      <input name="id" onChange={onChangeHandler} value={id} />
      <label> Name: </label>
      <input value={name}
        className={name.length < 6 ? 'input-error' : 'input-valid'}
        //style={name.length < 6 ? inputError : inputValid}
        // style={
        //   name.trim().length < 6
        //     ? { backgroundColor: "red" }
        //     : { backgroundColor: "green" }
        // }
        name="name"
        onChange={onChangeHandler}
      />
      <button type="button" onClick={submitCreateForm}> Create </button>
      <button type="button" onClick={submitUpdateForm}> Update </button>
      
    </div>
  );
}

function Table({ personArray, setPersonArray, setEditPerson }) {
  // let {personArray} = prop
  // [
  //   { id: 1, name: "A1" },
  //   { id: 2, name: "A2" },
  //   { id: 3, name: "A3" },
  //   { id: 4, name: "A4" }
  // ];
  // lap va tra ve mang moi, voi phan tu return
  // let newArr = personArray.map(p => {
  //   return (<tr>
  //     <td>{p.id}</td>
  //     <td>{p.name}</td>
  //   </tr>)
  // })
  // console.log(newArr)
  const deleteRow = (id) => {
    let newArr = personArray.filter(p => p.id !== id)
    setPersonArray(newArr)
  }

  return (
    <table border={1}>
      <thead>
        <tr>
          <th>ID</th>
          <th>Name</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {personArray.map((p) => {
          return (
            <tr key={p.id}>
              <td>{p.id}</td>
              <td>{p.name}</td>
              <td><button onClick={() => setEditPerson(p)}
              type='button'>Edit</button>
              <button onClick={() => deleteRow(p.id)}
              type='button'>Delete</button>
              </td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
}

function Hi(prop) {
  //properties - prop:js object
  console.log(prop);
  let { id, name } = prop; //es6: destruction
  return (
    <>
      <h1>Hello ReactJs {id}</h1>
      <p>Hello ReactJs</p>
    </>
  );
}
//function component
function Hello(prop) {
  // let name = prop.name
  // let id = prop.abc
  let { abc: id, name } = prop;
  return (
    <div>
      <h1>Hello React Js </h1>
      <p>
        {name} {id}
      </p>
    </div>
  );
}

export default TestPage;
