import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { deleteUserAPI, searchUserAPI } from "../../service/user.service";
import { Button, IconButton } from "@mui/material";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import { useDispatch, useSelector } from "react-redux";
import { searchThunk } from "../../redux/userSlice";

function ListUser() {
  let navigate = useNavigate();
  const dispatch = useDispatch();

  let { users, recordsFiltered, totalPage }
    = useSelector((store) => store.user)

  // let [users, setUsers] = useState([]);
  let [search, setSearch] = useState({
    start: 0,
    length: 5,
    search: {
      value: "",
    },
  });

  // let [totalPage, setTotalPage] = useState(0);

  const handleSearch = (e) => {
    setSearch({
      ...search,
      search: {
        value: e.target.value,
      },
    });
  };

  useEffect(() => {
    fetchData();
  }, [search]);

  const fetchData = async () => {
    dispatch(searchThunk(search))
  };

  const handleDelete = async (id) => {
    const confirmed = window.confirm('Are you sure you want to delete?');
    if (confirmed) {
      try {
        await deleteUserAPI(id)
        fetchData()
      } catch (e) {
        console.log(e);
      }
    }
  }

  const handleChangeSize = (e) => {
    const size = e.target.value;
    setSearch({
      ...search,
      length: parseInt(size),
    });
  };

  const handleNext = () => {
    setSearch({
      ...search,
      start: search.start + search.length,
    });
  };

  const handlePrev = () => {
    setSearch({
      ...search,
      start: search.start > 0 ? search.start - search.length : 0,
    });
  };

  const handlePage = (index) => {
    setSearch({
      ...search,
      start: index * search.length,
    });
  };

  return (
    <div>
      {/* <Link to="/user/create">Create User</Link> */}
      <Button to='/user/create' component={Link} variant="outlined" startIcon={<AddCircleIcon />}>User</Button>
      <div>
        <input onChange={handleSearch} />
      </div>

      <table border={1}>
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Phone</th>
            <th>Address</th>
            <th>Roles</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {users.map((u) => {
            return (
              <tr key={u.id}>
                <td>{u.id}</td>
                <td>{u.name}</td>
                <td>{u.phone}</td>
                <td>{u.address}</td>
                <td>{u.roles}</td>
                <td>
                  <IconButton onClick={() => navigate(`/user/update/${u.id}`)} variant="outlined"><EditIcon /></IconButton>|
                  <IconButton onClick={() => handleDelete(u.id)} variant="outlined" ><DeleteIcon /></IconButton>
                  {/* <button onClick={() => navigate(`/user/update/${u.id}`) }>Edit</button>
                  |<button onClick={()=> handleDelete(u.id)}>Delete</button> */}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <select onChange={handleChangeSize}>
        <option value="5">5</option>
        <option value="10">10</option>
        <option value="20">20</option>
      </select>
      <button onClick={handlePrev}>Prev</button>
      {Array.apply(null, { length: totalPage }).map((_, index) => {
        return (
          <button key={index} onClick={() => handlePage(index)}>
            {index}
          </button>
        );
      })}
      <button onClick={handleNext}>Next</button>
      {/* <p>Tong so trang: {totalPage}</p> */}
    </div>
  );
}

export default ListUser;
