import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { deletePostAPI, searchPostAPI } from "../../service/post.service";
import { Button, IconButton } from "@mui/material";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import { useDispatch, useSelector } from "react-redux";
import { searchThunk } from "../../redux/postSlice";

function ListPost() {
  let navigate = useNavigate()
  const dispatch = useDispatch();

  let { posts, recordsFiltered, totalPage }
    = useSelector((store) => store.post)

  // let [posts, setPosts] = useState([]);
  let [search, setSearch] = useState({
    start: 0,
    length: 5,
    search: {
      value: "",
    },
  });

  // let [totalPage, setTotalPage] = useState(0);

  // load component thi goi callback
  useEffect(() => {
    fetchData();
  }, [search]);

  const fetchData = async () => {
    dispatch(searchThunk(search))
  };

  const handleDelete = async (id) => {
    const confirmed = window.confirm('Are you sure you want to delete?');
    if (confirmed) {
      try {
        await deletePostAPI(id)
        fetchData()
      } catch (e) {
        console.log(e);
      }
    }
  }

  const handleSearch = (e) => {
    setSearch({
      ...search,
      search: {
        value: e.target.value,
      },
    });
  };

  const handleNext = () => {
    setSearch({
      ...search,
      start: search.start + search.length,
    });
  };

  const handlePrev = () => {
    setSearch({
      ...search,
      start: search.start > 0 ? search.start - search.length : 0,
    });
  };

  const handlePage = (index) => {
    setSearch({
      ...search,
      start: index * search.length,
    });
  };

  const handleChangeSize = (e) => {
    const size = e.target.value;
    setSearch({
      ...search,
      length: parseInt(size),
    });
  };

  return (
    <div>
      <Button to='/post/create' component={Link} variant="outlined" startIcon={<AddCircleIcon />}>Post</Button>
      <div>
        <input onChange={handleSearch} />
      </div>

      <table border={1}>
        <thead>
          <tr>
            <th>ID</th>
            <th>Title</th>
            <th>Description</th>
            <th>Image</th>
            <th>Category</th>
            <th>Create By</th>
            <th>Create Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {posts.map((p) => {
            return (
              <tr key={p.id}>
                <td>{p.id}</td>
                <td>{p.title}</td>
                <td>{p.description}</td>
                <td>
                  {/* ? null hay undef */}
                  {p.images?.map(image => (
                    <img src={`http://52.193.212.182:8080/image/${image}`} width={100} key={image} />
                  ))}

                </td>
                <td>{p.categoryName}</td>
                <td>{p.createBy}</td>
                <td>{p.createdDate}</td>
                <td>
                  <IconButton onClick={() => navigate(`/post/update/${p.id}`)} variant="outlined"><EditIcon /></IconButton>|
                  <IconButton onClick={() => handleDelete(p.id)} variant="outlined" ><DeleteIcon /></IconButton>
                  {/* <button onClick={() => navigate(`/post/update/${p.id}`)} >Edit</button>   */}
                  {/* <button onClick={() => handleDelete(p.id)}>Delete</button> */}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <select onChange={handleChangeSize}>
        <option value="5">5</option>
        <option value="10">10</option>
        <option value="20">20</option>
      </select>
      <button onClick={handlePrev}>Prev</button>
      {Array.apply(null, { length: totalPage }).map((_, index) => {
        return <button key={index} onClick={() => handlePage(index)}>{index}</button>;
      })}
      <button onClick={handleNext}>Next</button>
      {/* <p>Tong so trang: {totalPage}</p> */}
    </div>
  );
}
export default ListPost;
