import { useEffect, useState } from "react";
import { axiosJmaster } from "../../util/axios";
import { useNavigate } from "react-router";
import { addCategpryAPI, addUserAPI, searchUserAPI } from "../../service/user.service";

  function AddUser() {
    let [user, setUser] = useState({ 
      name: "",
      phone:"",
      password:"",
      address:"",
      roles:["ROLE_ADMIN"]
    });


  let handleChangeText = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  let navigate = useNavigate()

  let handleSubmit = async () => {
    await addUserAPI(user)
    navigate("/users") 
 };

  return (
    <div>
        <h1>New User</h1>
      <div>
        <label>Name: </label>
        <input name="name" onChange={handleChangeText} />

        <label>Phone: </label>
        <input name="phone" onChange={handleChangeText} />

        <label>Address: </label>
        <input name="address" onChange={handleChangeText} />

        <label>PassWord: </label>
        <input name="password" onChange={handleChangeText} />

        <label>Roles: </label>
        <input name="roles" onChange={handleChangeText} />
        {/* <select  name="roles" onChange={handleChangeText}>
          {roles.map(c => {
            return <option key={c.id} value={c.id}>{c.name}</option>
          })}
        </select> */}

        <button onClick={handleSubmit}>Save</button>
      </div>
    </div>
  );
}
export default AddUser;
