import { Link } from "react-router-dom";
import { ThemeContext } from "../../context/ThemeContext";
import React, { useContext, useMemo } from "react";
import { AuthContext } from "../../context/AuthContext";
import { useMe } from "../../hook/useAuth";
import { useDispatch, useSelector } from 'react-redux'
import { incNumber, increment, incrementAsync } from "../../redux/counterSlice";
import { Button, Tab, Tabs } from "@mui/material";
import ExitToAppIcon from '@mui/icons-material/ExitToApp';


function Menu() {
  let { mode, setMode } = useContext(ThemeContext)
  let { setAuth, user } = useContext(AuthContext)
  // co the xai ham hook
  const u = useMe()

  const counter = useSelector((store) => store.counter) //doc thuoc tinh store
  const dispatch = useDispatch(); // goi ham reducer

  console.log(counter);

  let handleMode = () => {
    setMode(mode === 'light' ? 'dark' : 'light')
    // dispatch(increment()) // goi action co type increatment
    // dispatch(incNumber(10)) //action = {type="counter/incNumber" , payload="10"}
    dispatch(incrementAsync(10))
  }

  let handleLogOut = () => {
    setAuth(false)
    localStorage.removeItem("token")
  }
  return (
    
    <Tabs
      textColor="secondary"
      indicatorColor="secondary"
      aria-label="secondary tabs example" 
      centered
    >
      {/* <div><h1>Xin chao {u.name} - {counter.x}</h1></div>  */}

      <Button onClick={handleMode}>{mode}</Button>
      <Tab label="Home"  to='/' component={Link}/>
      <Tab label="Category"  to='/categories' component={Link}/>
      <Tab label="Users"  to='/users' component={Link}/>
      <Tab label="Post"  to='/posts' component={Link}/>
      <Button onClick={handleLogOut} endIcon={<ExitToAppIcon />}>LogOut</Button>
    </Tabs>
    
    // <ul>
    //   
    //   <li>
    //     <button onClick={handleMode}>{mode}</button>
    //   </li>
    //   <li>
    //     <Link to="/">Home</Link>
    //   </li>
    //   <li>
    //     <Link to="/categories">Category</Link>
    //   </li>
    //   <li>
    //     <Link to="/users">Users</Link>
    //   </li>
    //   <li>
    //     <Link to="/posts">Post</Link>
    //   </li>
    //   <li>
    //     <button onClick={handleLogOut}>LogOut</button>
    //   </li>
    // </ul>
  );
}
export default Menu;
