import { axiosJmaster } from "../util/axios";

//chuyen toan bo ham goi API
export const getCategoryAPI = (id) => {
  let config = {
    method: "get",
    maxBodyLength: Infinity,
    url: "/category/" + id,
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.log(error);
    });
};

export let addCategpryAPI = (category) => {
  let data = JSON.stringify(category);

  let config = {
    method: "post",
    maxBodyLength: Infinity,
    url: "/admin/category/add",
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
    data: data,
  };
  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.log(error);
    });
};

export let updateCategoryAPI = (category) => {
  let data = JSON.stringify(category);

  let config = {
    method: "put",
    maxBodyLength: Infinity,
    url: "/admin/category/update",
    headers: {
      "Content-Type": "application/json",
      Authorization: 
      "Bearer " + localStorage.getItem("token"),
    },
    data: data,
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.log(error);
    });
};

export const deleteCategoryAPI = (id) => {
  let config = {
    method: "delete",
    maxBodyLength: Infinity,
    url: "/admin/category/delete?id=" + id,
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.log(error);
      throw error
    });
};

export function searchCategoryAPI(search) {
  let data = JSON.stringify(search);

  let config = {
    method: "post",
    maxBodyLength: Infinity,
    url: "/category/search",
    headers: {
      "Content-Type": "application/json",
    },
    data: data,
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      let body = response.data;
      return body;
    })
    .catch((error) => {
      console.log(error);
    });
}
