import { createSlice } from "@reduxjs/toolkit";
import { searchUserAPI } from "../service/user.service";

export const userSlice = createSlice({
  name: "user",
  initialState: {
    users: [],
    recordsFiltered: 0,
    totalPage: 0
  },
  reducers: {
    setUsers: (state, action) => {
      state.users = action.payload
      // return action.payload; //payload === initialState
    },
    setRecordsFiltered: (state, action) => {
      state.recordsFiltered = action.payload
    },
    setTotalPage: (state, action) => {
      state.totalPage = action.payload
    },
    setPage: (state, action) => {
      const { recordsFiltered, totalPage } = action.payload
      state.recordsFiltered = recordsFiltered
      state.totalPage = totalPage
    }
  },
});
export const { setUsers, setRecordsFiltered, setTotalPage, setPage } = userSlice.actions;

export default userSlice.reducer;

// middleware: thunk 
export const searchThunk = (searchDTO) => {
  return async (dispatch, getState) => {
    //async
    try {
      let body = await searchUserAPI(searchDTO);

      console.log(body.recordsFiltered);
      console.log(body.data);
  
      //tinh tong so trang

      let totalPage = Math.ceil(body.recordsFiltered / searchDTO.length);

      dispatch(setUsers(body.data))
      dispatch(setPage({ totalPage, recordsFiltered: body.recordsFiltered }))
    } catch (e) {
      console.log(e);
    }
    }
  };


