import { axiosJmaster } from "../util/axios";

//goi Api
export const meAPI = () => {
  var config = {
    method: "get",
    url: "/member/me",
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    }
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
}

export function searchUserAPI(search) {
  let data = JSON.stringify(search);

  let config = {
    method: "post",
    maxBodyLength: Infinity,
    url: "/admin/accounts",
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
    data: data,
  };

  return axiosJmaster
    .request(config)
    .then((response) => {
      return response.data;
    })
    .catch((error) => {
      console.log(error);
    });
}

