import { useState } from "react";
import { axiosJmaster } from "../../util/axios";
import { useNavigate } from "react-router";
import { addCategpryAPI } from "../../service/category.service";
import { toast } from 'react-toastify';
import ViewCategory from "./ViewCategory";

function AddCategory() {
  let [category, setCategory] = useState({ name: "" });

  let handleChangeName = (e) => {
    setCategory({ ...category, name: e.target.value });
  };

  let navigate = useNavigate()

  let handleSubmit = async () => {
    await addCategpryAPI(category)
    toast("Them thanh cong!")
    navigate("/categories") 
 };

  return (
    <div>

        <h1>New Category</h1>
        <ViewCategory/>
      <div>
        <label>Name: </label>
        <input name="name" onChange={handleChangeName} />

        <button onClick={handleSubmit}>Save</button>
      </div>
    </div>
  );
}
export default AddCategory;
